body {
  height: 100vh;
}

.root {
  height: 100%;
}

.App {
  min-height: calc(100% - 173px);
}

a {
  color: #f44336;
}

img {
  max-width: 100%;
}

nav {
  background-color: #f44336;
}

nav a {
    color: white;
  }

nav a:active {
      color: #f44336;
      opacity: 0.8;
    }

footer {
  background-color: #b9b6b6;
}

pre {
  border-left: 4px solid #f44336;
}

pre > code {
    display: block;
    padding: 0.5em;
    line-height: normal;
    line-height: initial;
  }

p > code {
  padding: 0.5em;
  font-size: 85%;
  line-height: 40px;
  border-radius: 3px;
  white-space: normal;
}

h1:not(:first-child), h2:not(:first-child), h3:not(:first-child), h4:not(:first-child), h5:not(:first-child), h6:not(:first-child) {
    padding-top: 35px;
  }
